.first-screen {
	position: relative;
	background: url("../img/first-screen-bg.jpg") left no-repeat;
	background-size: cover;
	padding: 240px 0 180px;
	color: $white;
	@include r(992) {
		padding: 150px 0 90px; }
	@include r(576) {
		padding: 130px 0 50px;
		text-align: center; }
	h1 {
		font-family: $title-font;
		font-weight: 900;
		text-transform: uppercase;
		max-width: 730px;
		font-size: 34px;
		line-height: 1.3em;
		padding-bottom: 20px;
		margin-bottom: 15px;
		position: relative;
		&::before, &::after {
			content: '';
			background: $crimson;
			width:  70px;
			position: absolute;
			left: 0; }
		&::before {
			height: 2px;
			bottom: 6px; }
		&:after {
			height: 1px;
			bottom: 0; }
		@include r(992) {
			max-width: none;
			font-size: 30px; }
		@include r(768) {
			font-size: 24px; }
		@include r(576) {
			font-size: 20px;
			&::before, &::after {
				left: 50%;
				transform: translateX(-50%); }
			br {
				display: none; } } }
	p {
		max-width: 550px;
		font-family: $title-font;
		margin-bottom: 45px;
		@include r(992) {
			max-width: none; }
		@include r(576) {
			font-size: 12px;
			letter-spacing: 0.05em; } }

	a {
		&.button {
			margin-right: 30px;
			@include r(576) {
				margin: 0 auto 15px; } } }
	a {
		text-align: center;
		&.more {
			margin: 0 auto; } } }

a {
	position: relative;
	display: inline-block;
	font-family: $button-fonts;
	&.button {
		color: $white;
		padding: 15px 65px;
		border-radius: 20px;
		background-image: linear-gradient( to top, #189224 0%, #48eb36 70%);
		box-shadow: 0 15px 20px 0 rgba(43, 187, 46, 0.25),inset 0px -3px 0px 0px rgba(16, 123, 29, 0.8);
		transition: ease all .3s;
		&:hover {
			background-image: linear-gradient( to top, #189224 0%, #48eb36 80%);
			box-shadow: 0 15px 20px 0 rgba(43, 187, 46, 0.3),inset 0px -4px 0px 0px rgba(16, 123, 29, 0.8);
			transition: ease all .3s; }
		&:active {
			transform: translateY(2px);
			background-image: linear-gradient( to top, #189224 0%, #48eb36 90%);
			box-shadow: 0 15px 20px 0 rgba(43, 187, 46, 0.2),inset 0px -3px 0px 0px rgba(16, 123, 29, 0.8);
			transition: ease all .3s; }
		@include r(576) {
			padding: 15px 45px; } }
	&.more {
		color: $white;
		&::before {
			content: '';
			position: absolute;
			transition: all .3s;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 1px;
			background-color: $white; }
		&:hover {
			&::before {
				width: 0; }
			&::after {
				transition: all .3s ease;
				transform: translateY(-50%) translateX(2px); } }
		&::after {
			content: url("../img/right-arrow.svg");
			background-position: center;
			position: absolute;
			width: 12px;
			height: 12px;
			right: -30px;
			top: 30%;
			transform: translateY(-50%);
			transition: all .3s ease; } } }
